// Like comment
export const addLike = id => dispatch => {
  axios
    .post("/comments/addLike/" + id)
    .then(res => this.props.history.push("/"))
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};

// Remove comment
export const removeLike = id => dispatch => {
  axios
    .post("/comments/removeLike/" + id)
    .then(res =>
      dispatch({
        type: GET_LIKES,
        payload: res.data
      })
    )
    .catch(err =>
      dispatch({
        type: GET_LIKES,
        payload: {}
      })
    );
};
